$card-slider-height-xs: 340px;
$card-slider-height-md: 500px;
$card-slider-height-lg: 500px;
$card-slider-sides-width: 51%;
$card-slider-sides-width-xs: 68%;
$card-slider-sides-top: 50px;
$card-slider-sides-top-xs: 60px;
$card-slider-center-width: 65%;
$card-slider-center-width-xs: 104%;

.section-welcome {
  background-image: url(../images/home/bg-welcome.png);
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  padding-top: 50px;
  padding-bottom: 3rem;
  height: auto;
  display: flex;
  align-items: center;
  @media (min-width: 992px) {
    padding-top: 100px;
  }
  .container {
    position: relative;
    .slider-cards {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      position: relative;
      width: 100%;
      height: $card-slider-height-xs;
      @media (min-width: 540px) {
        height: $card-slider-height-md;
      }
      @media (min-width: 992px) {
        height: $card-slider-height-lg;
      }
      .slider-card-item {
        display: flex;
        justify-content: center;
        width: $card-slider-center-width;
        height: 100%;
        border-radius: 0.5rem;
        transform-origin: center center;
        transition: .8s;
        position: absolute;
        top: 0;
        z-index: 3;
        overflow: hidden;
        flex-direction: column;
        padding: 4rem 2rem;
        @media (min-width: 992px) {
          padding: 3rem 2rem;
        }
        .row {
          background-color: $white;
          border-radius: 0.5rem;
          box-shadow:0 30px 50px rgba(0,0,0,0.1);
          padding: 1.5rem 0;
          height: 100%;
          @media (min-width: 992px) {
            padding: 1rem 0;
          }
          .img-product {
            max-width: 100%;
            height: auto;
            transform: scale(1);
            @media (min-width: 992px) {
              max-width: 70%;
              height: auto;
              transform: scale(1.4);
            }
          }
        }
        * {
          pointer-events: none;
        }
        &--left {
          z-index: 1;
          transform: translate(-36%, 0px);
          @media (max-width: 991.98px) {
            width: 105%;
          }
          @media (min-width: 768px) {
            transform: translate(-15%, 0px) scale(0.9);
          }
          .row {
            opacity: 0;
            transition: opacity 1s;
            @media (min-width: 992px) {
              opacity: 0.6;
            }
            * {
              opacity: 0;
              transition: opacity 1s;
            }
          }
        }
        &--center {
          z-index: 3;
          width: 105%;
          transform: translate(0%, 0px);
          @media (min-width: 992px) {
            width: 65%;
          }
          * {
            pointer-events: initial;
            opacity: 1;
            transition: opacity 1s;
          }
        }
        &--right {
          z-index: 1;
          transform: translate(36%, 0px);
          @media (max-width: 991.98px) {
            width: 105%;
          }
          @media (min-width: 768px) {
            transform: translate(15%, 0px) scale(0.9);
          }
          .row {
            opacity: 0;
            transition: opacity 1s;
            @media (min-width: 992px) {
              opacity: 0.6;
            }
            * {
              opacity: 0;
              transition: opacity 1s;
            }
          }
        }
      }
    }
    .content-button-slider {
      position: absolute;
      right: 1rem;
      bottom: 0;
      z-index: 4;
      @media (min-width: 992px) {
        bottom: 25%;
      }
      button {
        width: 35px;
        height: 35px;
        border: none;
        border-radius: 50px;
        background: transparent;
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        opacity: 0.6;
        transition: all 0.3s;
        &.prev-slider {
          background-image: url(../images/home/flecha-izq.png);
          margin-right: 0.5rem;
        }
        &.next-slider {
          background-image: url(../images/home/flecha-der.png);
          margin-left: 0.5rem;
        }
        &:hover {
          opacity: 1;
        }
      }
    }
  }
}